<template>
	<div>
		<title-bar title=""></title-bar>
		<div class="content-box-title-bottom">
			<div class="mdui-tab mdui-color-theme mdui-tab-full-width" mdui-tab>
			  <a href="#example1-tab1" class="mdui-ripple mdui-ripple-white">
