			    <label></label>
			  </a>
			  <a href="#example1-tab2" class="mdui-ripple mdui-ripple-white">
			    <img src="../assets/message1.svg" />
			    <label></label>
			  </a>
			</div>
			<div id="example1-tab1" class="mdui-p-a-2">
				<!--  -->
<!--				<div class="animate__animated animate__lightSpeedInRight">-->
				<div>
					<ul class="mdui-list">
					  <li class="mdui-list-item mdui-ripple ">
						  <label class="mdui-radio" style="width: 100%;">
							  <input type="radio" name="helpType" checked value="1"/>
							  <i class="mdui-radio-icon"></i>
							  
						  </label>
					  </li>
					  <li class="mdui-list-item mdui-ripple">
						  <label class="mdui-radio" style="width: 100%;">
							  <input type="radio" name="helpType" value="2"/>
							  <i class="mdui-radio-icon"></i>
							  
						  </label>
					  </li>
					  <li class="mdui-list-item mdui-ripple">
						  <label class="mdui-radio" style="width: 100%;">
							  <input type="radio" name="helpType" value="3"/>
							  <i class="mdui-radio-icon"></i>
							  
						  </label>
					  </li>
					  <li class="mdui-list-item mdui-ripple">
						  <label class="mdui-radio" style="width: 100%;">
							  <input type="radio" name="helpType" value="0"/>
							  <i class="mdui-radio-icon"></i>
							  
						  </label>
					  </li>
					</ul>
				</div>
				<div class="">
					<div class="push-box">
						<textarea v-model="requestData.content"></textarea>
						<div class="image-select-box">
							<span v-for="(img, index) in uploadImages">
								<div class="image-select-remove mdui-ripple" @click="removeImg(index)">
									<i class="mdui-icon material-icons">close</i>
								</div>
								<img :src="ser.ctx + img.url">
							</span>
							<img src="../assets/add.svg" @click="putImage">
						</div>
					</div>
					<button class="mdui-btn mdui-btn-raised mdui-ripple mdui-color-theme-accent mdui-btn-block" @click="submitHelp"></button>
				</div>
			</div>
			<div id="example1-tab2" class="mdui-p-a-2">
				<div class="service-message-list">
					<div class="service-message-item" v-for="item in serviceMessageList">
						{{item.createTime}}
						<div style="border-bottom: #ff9800 solid 1px; padding-bottom:10px; margin-bottom: 10px;" v-text="item.content">
						</div>
						<div>
							<span>:</span>
							<div>{{item.replyContent ? item.replyContent : ''}}</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div style="display: none;">
			<input type="file" ref="fileInput" accept="image/*" @change="doUpload"/>
		</div>
		<bottom-nav act="service"></bottom-nav>
		<api-loading :class="{'loading-open': loadingShow}"></api-loading>
	</div>
</template>

<script>
	import titleBar from './titleBar.vue'
	import bottomNav from './bottomNav.vue'
	import apiLoading from './apiLoading.vue'
	export default {
		name: "service",
		components:{
			titleBar,
			bottomNav,
			apiLoading
		},
		data(){
			return {
				loadingShow: false,
				uploadImages: [
				],
				serviceMessageList: [
					// {
					// 	createTime: "2020-01-01 11:11:11",
					// 	content: "",
					// 	replyContent: ""
					// },{
					// 	createTime: "2020-01-01 11:11:11",
					// 	content: "",
					// 	replyContent: ""
					// }
				],
				requestData: {
					content: '',
					images: []
				}
			}
		},
		created(){
			this.listHelps()
			setTimeout(() => {
				mdui.mutation()
			}, 100)
		},
		methods: {
			listHelps(){
				this.$data.loadingShow = true
				this.ser.get("api/v1/mobile/services").then(res => {
					this.$data.serviceMessageList = res.data
				}).finally(err => {
					this.$data.loadingShow = false
				})
			},
			submitHelp(){
				this.$data.loadingShow = true
				this.ser.post("api/v1/mobile/services", this.$data.requestData).then(res => {
					this.$data.requestData = {content: '',images: []}
					this.$data.uploadImages = []
					mdui.snackbar("")
				}).finally(err => {
					this.$data.loadingShow = false
				})
			},
			removeImg(i){
				this.$data.uploadImages.splice(i, 1)
			},
			putImage(){
				return this.$refs.fileInput.click();
			},
			doUpload(){
				let file = this.$refs.fileInput.files[0];
				this.$refs.fileInput.value = ''
				let formData = new FormData();
				formData.append("file", file)
				var options = {  // axios
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				}
				this.$data.loadingShow = true
				this.ser.post("comm/upload", formData, options).then(res => {
					this.$data.uploadImages.push({
						url: "/comm/display/" + res.data.responseBody.url
					})
					this.$data.requestData.images.push("/comm/display/" + res.data.responseBody.url)
				}).catch(err => {
					this.$data.loadingShow = false
				})
			}
		}
	}
</script>

