<template>
	<div>
		<title-bar title="" backBtn="true"></title-bar>
		<div>
			<div class="mdui-tab" mdui-tab>
				<a href="#example1-tab1" class="mdui-ripple"></a>
				<a href="#example1-tab2" class="mdui-ripple"></a>
				<a href="#example1-tab3" class="mdui-ripple"></a>
			</div>
			<div id="example1-tab1">
				<div style="text-align: center; margin-top: 10px" v-if="buyGoodsInfo.using.length === 0"></div>
				<ul class="mdui-list mdui-list-dense" mdui-collapse="{accordion: true}">
					<li class="mdui-collapse-item" v-for="item in buyGoodsInfo.using">
						<div class="mdui-collapse-item-header mdui-list-item mdui-ripple">
							<div class="mdui-list-item-avatar"><img :src="ser.ctx + item.picture"/></div>
							<div class="mdui-list-item-content">
								<div class="mdui-list-item-title" v-text="item.goodsName"></div>
								<div class="mdui-list-item-text mdui-list-item-three-line">
									ID: {{item.goodsId}}&nbsp;:{{item.price}}
									<br/>
									: {{item.statusStr}}
									<br/>
									: {{item.buyDate}}
								</div>
							</div>
						</div>
						<ul class="mdui-collapse-item-body mdui-list mdui-list-dense" style="background-color: rgb(66, 66, 66);">
						</ul>
					</li>
				</ul>
			</div>
			<div id="example1-tab2">
				<div style="text-align: center; margin-top: 10px" v-if="buyGoodsInfo.buying.length === 0"></div>
				<ul class="mdui-list mdui-list-dense" mdui-collapse="{accordion: true}">
					<li class="mdui-collapse-item" v-for="(item, index) in buyGoodsInfo.buying">
						<div class="mdui-collapse-item-header mdui-list-item mdui-ripple">
							<div class="mdui-list-item-avatar"><img :src="ser.ctx + item.picture"/></div>
							<div class="mdui-list-item-content">
								<div class="mdui-list-item-title" v-text="item.goodsName"></div>
								<div class="mdui-list-item-text mdui-list-item-three-line">
									ID: {{item.goodsId}}&nbsp;:{{item.price}}
									<br/>
									: {{item.statusStr}}
									<br/>
									: {{item.buyDate}}
								</div>
							</div>
							<i class="mdui-collapse-item-arrow mdui-icon material-icons">keyboard_arrow_down</i>
						</div>
						<ul class="mdui-collapse-item-body mdui-list mdui-list-dense" >
							<li class="mdui-list-item mdui-ripple upload-payd-img" style="padding-left:2rem;" v-if="item.status === 4" @click="putPayImage(item, index)">
								<i class="mdui-list-item-icon mdui-icon material-icons">file_upload</i>
								<div class="mdui-list-item-content"></div>
							</li>
							<li class="mdui-list-item mdui-ripple show-qrcode-img" style="padding-left:2rem;" v-if="item.status === 4" @click="showUserQrCode(item.fromUserId)">
								<i class="mdui-list-item-icon mdui-icon material-icons">attach_money</i>
								<div class="mdui-list-item-content"></div>
							</li>
							<li class="mdui-list-item mdui-ripple show-qrcode-img" style="padding-left:2rem;" v-if="item.status === 5" @click="showUserInfo(item.fromUserId)">
								<i class="mdui-list-item-icon mdui-icon material-icons">phone_iphone</i>
								<div class="mdui-list-item-content"></div>
							</li>
						</ul>
					</li>
				</ul>
			</div>
			<div id="example1-tab3">
				<div style="text-align: center; margin-top: 10px" v-if="buyGoodsInfo.outing.length === 0"></div>
				<ul class="mdui-list mdui-list-dense" mdui-collapse="{accordion: true}">
					<li class="mdui-collapse-item" v-for="item in buyGoodsInfo.outing">
						<div class="mdui-collapse-item-header mdui-list-item mdui-ripple">
							<div class="mdui-list-item-avatar"><img :src="ser.ctx + item.picture"/></div>
							<div class="mdui-list-item-content">
								<div class="mdui-list-item-title" v-text="item.goodsName"></div>
								<div class="mdui-list-item-text mdui-list-item-three-line">
									ID: {{item.goodsId}}&nbsp;:{{item.price}}
									<br/>
									: {{item.statusStr}}
									<br/>
									: {{item.buyDate}}
								</div>
							</div>
							<i class="mdui-collapse-item-arrow mdui-icon material-icons" v-if="item.status === 5">keyboard_arrow_down</i>
						</div>
						<ul class="mdui-collapse-item-body mdui-list mdui-list-dense" >
							<li class="mdui-list-item mdui-ripple show-qrcode-img" style="padding-left:2rem;" v-if="item.status === 5" @click="showUserInfo(item.nextUserId)">
								<i class="mdui-list-item-icon mdui-icon material-icons">phone_iphone</i>
								<div class="mdui-list-item-content"></div>
							</li>
							<li class="mdui-list-item mdui-ripple show-qrcode-img" style="padding-left:2rem;" v-if="item.status === 5" @click="check(item.id)">
								<i class="mdui-list-item-icon mdui-icon material-icons">attach_money</i>
								<div class="mdui-list-item-content"></div>
							</li>
						</ul>
					</li>
				</ul>
			</div>
		</div>
		<div style="display: none">
			<input type="file" accept="image/*" ref="uploadInput" @change="doUpload" />
		</div>
		<api-loading :class="{'loading-open': loadingShow}"></api-loading>
	</div>
</template>

<script>
	import titleBar from './titleBar.vue'
	import apiLoading from './apiLoading.vue'

	export default {
		name: "persionBuys",
		components: {
			titleBar,
			apiLoading
		},data(){
			return {
				loadingShow: false,
				buyGoodsInfo: {
					using: [],
					buying: [],
					outing: []
				},
				uploadPayImageItemIndex: 0
			}
		},created() {
			this.listBuyGoodsInfo()
			setTimeout(() => {
				mdui.mutation()
			}, 100)
		}, methods:{
			check(makeId){
				let td = this.$data
				let _t = this
				this.$layer.open({
					btn: ['', ''],
					content: '',
					className: 'good luck1',
					shade: true,
					yes() {
						td.loadingShow = true
						_t.ser.post("api/v1/mobile/goods/check", {
							id: makeId
						}).then(res => {
							_t.listBuyGoodsInfo()
						}).catch(()=>{
							td.loadingShow = false
						})
					}
				})
			},
			showUserQrCode(userId){
				this.$router.push({name: 'userQrCode', params: {userId: userId}})
			},
			showUserInfo(userId){
				this.$data.loadingShow = true
				this.ser.get("api/v1/mobile/user/" + userId + "/info").then(res => {
					let phone = res.data.phone
					this.$layer.open({
						content: ':' + res.data.phone,
						btn: ['', ''],
						skin: 'footer',
						no: (index) => {
							window.location.href = 'tel:' + phone
						}
					})
				}).finally(() => {
					this.$data.loadingShow = false
				})
			},
			putPayImage(item, index){
				this.$data.uploadPayImageItemId = index;
				return this.$refs.uploadInput.click()
			},
			doUpload(){
				let item = this.$data.buyGoodsInfo.buying[this.$data.uploadPayImageItemIndex]
				let file = this.$refs.uploadInput.files[0]
				this.$refs.uploadInput.value = ''
				let formData = new FormData();
				formData.append("file", file)
				let options = {  // axios
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				}
				this.$data.loadingShow = true
				this.ser.post("comm/upload", formData, options).then(res => {
					this.ser.post("api/v1/mobile/goods/buys/upload-pay-image", {
						paydImage: "/comm/display/" + res.data.responseBody.url,
						id: item.id
					}).then(res => {
						mdui.snackbar("")
						item.status = 5
						item.statusStr = ""
					}).finally(()=>{
						this.$data.loadingShow = false
					})
				}).catch(err => {
					this.$data.loadingShow = false
				})
			},
			listBuyGoodsInfo(){
				this.$data.loadingShow = true
				this.ser.get("api/v1/mobile/goods/buys").then(res => {
					for(let i in res.data){
						let item = res.data[i];
						if(item.status === 1){
							this.$data.buyGoodsInfo.using.push(item);
						}else if((item.status === 4 && item.statusStr === '') || (item.status === 5 && item.statusStr === '')){
							this.$data.buyGoodsInfo.buying.push(item);
						}else if((item.status === 4 && item.statusStr === '') || (item.status === 5 && item.statusStr === '') || item.status === 2){
							this.$data.buyGoodsInfo.outing.push(item);
						}
					}
				}).finally(() => {
					this.$data.loadingShow = false
				})
			}
		}
	}
</script>

